import React, {Component} from 'react';
import {render} from 'react-dom';
import { Input, Row, Col, Button, Select, DatePicker, Breadcrumb,Form,Upload, message, Modal, Table, Tooltip, Checkbox, Switch} from 'antd';
import moment from 'moment';
import { UploadOutlined } from '@ant-design/icons';
import {request} from '../../../../utils/request';
import Top from '../../../../component/top/index';
import cookie from 'react-cookies';
import PolicyManagementMenu from "../../../../component/policyManagementMenu/index";
import Title from "../../../../component/title/index";
import './index.css';

import E from 'wangeditor'



const { Option } = Select;
const {Search,TextArea} = Input;
const layout = {
    labelCol: {span: 4},
    wrapperCol: {span: 18},
};

const uploadUrl = 'http://web.js.policy.com/api/common/upload-file';

const validateMessages = {
    required: '!',
    types: {
        email: 'Not a validate email!',
        number: 'Not a validate number!',
    },
    number: {
        range: 'Must be between ${min} and ${max}',
    },
};

const { RangePicker } = DatePicker;

class AddProject extends Component {
    constructor(props){
        super(props);
        this.state = {
            id:props.match.params ? props.match.params.id : null,
            policyVisible:false
        }
    }

    componentDidMount(){
        this.createEditor("editorElem1","editorElemContent");
        this.createEditor("editorElem2","editorElem2Content");
        this.createEditor("editorElem3","editorElem3Content");
        this.createEditor("editorElem4","editorElem4Content");
        this.createEditor("editorElem5","editorElem5Content");
        this.createEditor("editorElem6","editorElem6Content");
        this.getDefalutData();
        this.columns = [
            {
                title: '',
                dataIndex: 'title',
                key: 'title',
                width: 180,
                render: (text, record) => {
                    return <Tooltip placement="topLeft" title={text}><a onClick={()=>this.props.history.push(`/policyPreview/${record.id}`)}>{text.length < 15 ? text : text.substr(0,15)+"..."}</a></Tooltip>
                }
            },
            {
                title: '',
                dataIndex: 'organization_label_str',
                key: 'organization_label_str',
                render: (text, record) => {
                    return <Tooltip placement="topLeft" title={text}><span>{text.length < 6 ? text : text.substr(0,6)+"..."}</span></Tooltip>
                }
            },
            {
                title: '',
                key: 'updated_date',
                dataIndex: 'updated_date',
                width: 130
            },
            {
                title: '',
                key: 'action',
                width:120,
                render: (text, record) => (<p align="center"><a onClick={()=>this.props.history.push(`/addProject/${record.id}`)}></a><a onClick={()=>this.showModal(record.id)} className="ml15"></a></p>),
            },
        ];

    }
    createEditor = (editorElem,editorContent) =>{
        const elem = this.refs[editorElem]; //editorElem
        //const submit = this.refs.submit; //
        const editor = new E(elem)  //new  editorElem
        editor.customConfig.uploadFileName = 'file'; //
        editor.customConfig.uploadImgServer = uploadUrl;//
        editor.customConfig.onchange = html => {
            this.setState({
                [editorContent]: html
            })
        };
        editor.customConfig.uploadImgHooks = {
            before: function (xhr, editor, files) {
                console.log(xhr, editor, files,"before")
            },
            success: function (xhr, editor, result) {
                console.log("");
                console.log(xhr, editor, result,"success")
            },
            fail: function (xhr, editor, result) {
                console.log("," + result);
                console.log(xhr, editor, result,"fail")
            },
            error: function (xhr, editor) {
                console.log("");
                console.log(xhr, editor,"error")
            },
            timeout: function (xhr, editor) {
                console.log("");
                console.log(xhr, editor,"timeout")
            },
            customInsert: function (insertImg, result, editor) {
                console.log(insertImg, result, editor, "file")
                if(result.success) {
                    var url = result.data.image_url  //
                    insertImg(url)
                }
            }
        };
        editor.create() //
    }
    getDefalutData = async() =>{
        const labelThemeData = await request('/common/get-all-policy-theme-label', 'POST'); //
        const labelTypeData = await request('/common/get-all-use-type-label', 'POST'); //
        const selectBelongData = await request('/common/get-all-belong-label', 'POST'); //
        const selectIndustryData = await request('/common/get-all-industry-label', 'POST'); //


        const themData = labelThemeData.data;
        const typeData = labelTypeData.data;
        const belongData = selectBelongData.data;
        const industryData = selectIndustryData.data;

        if (themData && themData.success && typeData && themData.success && belongData && belongData.success && industryData && industryData.success) {
            const allItem = {id: 0,name: ""};
            themData.data.unshift(allItem);
            typeData.data.unshift(allItem);
            belongData.data.unshift(allItem);
            industryData.data.unshift(allItem);
            this.setState({
                themeData: themData.data,
                typeData: typeData.data,
                belongData: belongData.data,
                industryData: industryData.data

            })
        }
        //
        if(this.state.id){
            const {data} = await request(`/policy/get/${this.state.id}`, 'GET'); //
            if(data){
                const {policy} = data;
                this.setState({
                    data,
                    release_date:policy.release_date,
                    content:policy.content
                });
                policy.release_date = moment(policy.release_date, 'YYYY-MM-DD');
                policy.life_date = moment(policy.life_date, 'YYYY-MM-DD');

                this.refs.form.setFieldsValue(policy);
                //editor.txt.html(policy.content);
                this.belongChange(policy.belong); //
            }
        }
    }
    onSubmit = async(values,url) => {
        const {release_date,life_date,editorContent,id,fileList} = this.state;
        values.release_date = release_date;
        values.life_date = life_date;
        values.content = editorContent;
        values.member_id = cookie.load("userId");
        values.username = cookie.load("userName");
        values.upload_file_list = fileList.map((item,idx)=>item.response.data.id);
        if(id){
            values.id = id;
        }
        const data = await request(this.state.id ? '/policy/update' : '/policy/add', 'POST',values);
        if(data.data && data.data.success){
            message.success(data.data.msg);
            setTimeout(()=>{
                this.props.history.push(url ? url+"/"+data.data.data.id : '/policyList');
            },2000);
        }else{
            message.error(data.data.msg);
        }
    }
    onFinish = async (values) => {
        console.log(values,this);
        values.status = 2;
        this.onSubmit(values);

    }
    onSave = async () => {
        let values = this.refs.form.getFieldsValue();
        values.status = 1;
        this.onSubmit(values);

    }
    onView = () =>{
        let values = this.refs.form.getFieldsValue();
        values.status = 1;
        this.onSubmit(values,"/policyPreview");
    }
    belongChange = async (value) => {
        const labelProductData = await request('/common/get-all-organization-label', 'POST', {belong_id: value}); //
        const productData = labelProductData.data;
        if (productData && productData.success) {
            this.setState({
                productData: productData.data
            })
        }
    }
    //
    onDateChange = (date,dateString) =>{
        this.setState({
            release_date:dateString
        })
    }
    onDateLifeChange = (date,dateString) =>{
        this.setState({
            life_date:dateString
        })
    }
    handleChange = (value) =>{
        console.log(`selected ${value}`);
    }
    handleUploadChange = info => {
        console.log(info,"info")
        let fileList = [...info.fileList];

        // 1. Limit the number of uploaded files
        // Only to show two recent uploaded files, and old ones will be replaced by the new
        fileList = fileList.slice(-2);

        // 2. Read from response and show file link
        fileList = fileList.map(file => {
            if (file.response) {
                // Component will show file.url as link
                file.url = file.response.url;
            }
            return file;
        });

        this.setState({ fileList });
    };
    showPolicy = () =>{
        this.setState({
            policyVisible:true
        })
    }
    handleOk = async(e) => {
        const deleteData = await request('/policy/del', 'POST',{id:this.state.id}); //
        if(deleteData.data && deleteData.data.success){
            message.success(deleteData.data.msg);
            this.setState({
                policyVisible: false,
                id:null
            });
            setTimeout(()=>{
                this.getTableData(this.state.formValues);
            },1000);
        }else{
            message.error(deleteData.data.msg);
        }
    };

    handleCancel = e => {
        console.log(e);
        this.setState({
            policyVisible: false,
        });
    };
    switchChange = (checked) =>{
        console.log(`switch to ${checked}`);
    }

    onSelectChange = selectedRowKeys => {
        console.log('selectedRowKeys changed: ', selectedRowKeys);
        this.setState({ selectedRowKeys });
    };

    render() {
        const {industryData,belongData,typeData,productData,id,tableData,selectedRowKeys} = this.state;
        const props = {
            //action: 'http://web.js.policy.com/api/common/upload-file',
            action:uploadUrl,
            onChange: this.handleUploadChange,
            multiple: true,
            data:{
                userId:cookie.load("userId"),
                userName:cookie.load("userName"),
                userType:cookie.load("userType"),
            },
            accept:".doc,.docx,application/msword,application/vnd.openxmlformats-officedocument.wordprocessingml.document,.pdf,ppt,.pptx,.xls,.xlsx,.pdf,.zip,.rar"
        };
        const rowSelection = {
            selectedRowKeys,
            type:"radio",
            onChange: this.onSelectChange,
        };

        return (
            <div className="addProject-template">
                <Top />
                <div className="addProject-label-box max-weight-box">
                <Row>
                    <Col span={4}>
                        <PolicyManagementMenu menu="projectList" current="projectList" />
                    </Col>
                    <Col span={20}>
                    <Title name={id ? "" : ""} />
                    <Breadcrumb separator=">">
                        <Breadcrumb.Item></Breadcrumb.Item>
                        <Breadcrumb.Item href="/projectList"></Breadcrumb.Item>
                        <Breadcrumb.Item>{id ? "" : ""} </Breadcrumb.Item>
                    </Breadcrumb>
                    <div className="label-box">
                        <Form.Provider>
                        <Form ref="form" {...layout} name="dynamic_rule" onFinish={this.onFinish} validateMessages={validateMessages}>
                            <Form.Item name="title" label="" rules={[{required: true}]}>
                                <Input />
                            </Form.Item>
                            <Form.Item name="belong1" label="" rules={[{required: true}]}>
                                <span></span>
                                <Button onClick={this.showPolicy}></Button>
                            </Form.Item>
                            <Form.Item name="belong" label="" rules={[{required: true}]}>
                                <Select onChange={this.belongChange}>
                                    {belongData ? belongData.map((item, idx) => <Option value={item.id}
                                                                                        key={item.id}>{item.name}</Option>) : ''}
                                </Select>
                            </Form.Item>
                            <Form.Item name="organization_label_list" label="" rules={[{required: true}]}>
                                <Select
                                    mode="multiple"
                                    style={{ width: '100%' }}
                                    onChange={this.handleChange}
                                >
                                    {productData ? productData.map((item, idx) => <Option value={item.id}
                                                                                          key={item.id}>{item.name}</Option>) : ''}
                                </Select>
                            </Form.Item>
                            <Form.Item name="organization_label_list" label="" rules={[{required: true}]}>
                                <Input />
                            </Form.Item>
                            <Form.Item name="release_date" label="">
                                <RangePicker onChange={this.onDateChange} />
                            </Form.Item>
                            <Form.Item name="use_type_list" label="" rules={[{required: true}]}>
                                <Select
                                    mode="multiple"
                                    style={{ width: '100%' }}
                                    onChange={this.handleChange}
                                >
                                    {typeData ? typeData.map((item, idx) => <Option value={item.id}
                                                                                    key={item.id}>{item.name}</Option>) : ''}

                                </Select>
                            </Form.Item>
                            <Form.Item name="industry_label_id_list" label="" rules={[{required: true}]}>
                                <Select
                                    mode="multiple"
                                    style={{ width: '100%' }}
                                    onChange={this.handleChange}
                                >
                                    {industryData ? industryData.map((item, idx) => <Option value={item.id}
                                                                                            key={item.id}>{item.name}</Option>) : ''}

                                </Select>
                            </Form.Item>
                            <Form.Item name="content" label="" required>
                                <div ref="editorElem1">
                                </div>
                            </Form.Item>
                            <Form.Item name="content" label="" required>
                                <div ref="editorElem2">
                                </div>
                            </Form.Item>
                            <Form.Item name="content" label="" required>
                                <div ref="editorElem3">
                                </div>
                            </Form.Item>
                            <Form.Item name="organization_label_list" label="">
                                <Input />
                            </Form.Item>
                            <Form.Item name="content" label="" required>
                                <div ref="editorElem4">
                                </div>
                            </Form.Item>
                            <Form.Item name="content" label="" required>
                                <div ref="editorElem5">
                                </div>
                            </Form.Item>
                            <Form.Item name="content" label="">
                                <div ref="editorElem6">
                                </div>
                            </Form.Item>
                            <Form.Item name="username" label="">
                                <Upload {...props} fileList={this.state.fileList}>
                                    <Button>
                                        <UploadOutlined /> 
                                    </Button>
                                </Upload>
                                <span>.doc/.docx/.ppt/.pptx/.xls/.xlsx/.pdf/.zip/.rar100M</span>
                            </Form.Item>
                            <p style={{fontWight:"bold",color:"#000",fontSize:"16px"}}><span style={{color: "#ff4d4f"}}>*</span></p>
                            <Row>
                                <Col span={4}><Checkbox></Checkbox></Col>
                                <Col span={10}><Form.Item name="username">
                                    <Input/>
                                </Form.Item></Col>
                            </Row>
                            <Row>
                                <Col span={4}><Checkbox></Checkbox></Col>
                                <Col span={20}><Form.Item name="username">
                                    <TextArea rows={4}/>
                                </Form.Item></Col>
                            </Row>
                            <Row>
                                <Col span={4}></Col>
                                <Col span={20}>
                                <table style={{width:"100%"}} className="label-table">
                                    <tr>
                                        <th></th>
                                        <th></th>
                                        <th></th>
                                    </tr>
                                    <tr>
                                        <td></td>
                                        <td>
                                            <Row>
                                                <Col span={4}>
                                                    <Select
                                                        style={{ width: '90%' }}
                                                    >
                                                        <Option value="" key=""></Option>
                                                        <Option value="=" key="=">=</Option>
                                                        <Option value="" key=""></Option>
                                                    </Select>
                                                </Col>
                                                <Col span={19}>
                                                    <Input/>
                                                </Col>
                                            </Row>
                                        </td>
                                        <td><Switch defaultChecked onChange={this.switchChange}/></td>
                                    </tr>
                                    <tr>
                                        <td></td>
                                        <td>
                                            <Select
                                                style={{ width: '50px',marginRight:"5px" }}
                                            >
                                                <Option value="" key=""></Option>
                                                <Option value="=" key="=">=</Option>
                                                <Option value="" key=""></Option>
                                            </Select>
                                            
                                            <Select
                                                style={{ width: '50px',marginLeft:"20px",marginRight:"5px" }}
                                            >
                                                <Option value="" key=""></Option>
                                                <Option value="=" key="=">=</Option>
                                                <Option value="" key=""></Option>
                                            </Select>
                                            
                                            <Select
                                                style={{ width: '50px',marginLeft:"20px",marginRight:"5px" }}
                                            >
                                                <Option value="" key=""></Option>
                                                <Option value="=" key="=">=</Option>
                                                <Option value="" key=""></Option>
                                            </Select>
                                            
                                        </td>
                                        <td><Switch defaultChecked onChange={this.switchChange}/></td>
                                    </tr>
                                    <tr>
                                        <td></td>
                                        <td>
                                            <Row>
                                                <Col span={4}>
                                                    <Select
                                                        style={{ width: '90%' }}
                                                    >
                                                        <Option value="" key=""></Option>
                                                        <Option value="=" key="=">=</Option>
                                                        <Option value="" key=""></Option>
                                                    </Select>
                                                </Col>
                                                <Col span={19}>
                                                    <Input suffix=""/>
                                                </Col>
                                            </Row>
                                        </td>
                                        <td><Switch defaultChecked onChange={this.switchChange}/></td>
                                    </tr>
                                    <tr>
                                        <td></td>
                                        <td>
                                            <Row>
                                                <Col span={4}>
                                                    <Select
                                                        style={{ width: '90%' }}
                                                    >
                                                        <Option value="" key=""></Option>
                                                        <Option value="=" key="=">=</Option>
                                                        <Option value="" key=""></Option>
                                                    </Select>
                                                </Col>
                                                <Col span={19}>
                                                    <Input suffix=""/>
                                                </Col>
                                            </Row>
                                        </td>
                                        <td><Switch defaultChecked onChange={this.switchChange}/></td>
                                    </tr>
                                    <tr>
                                        <td></td>
                                        <td>
                                            <Select
                                                mode="multiple"
                                                style={{ width: '100%' }}
                                                onChange={this.handleChange}
                                            >
                                                {industryData ? industryData.map((item, idx) => <Option value={item.id}
                                                                                                        key={item.id}>{item.name}</Option>) : ''}

                                            </Select>
                                        </td>
                                        <td><Switch defaultChecked onChange={this.switchChange}/></td>
                                    </tr>
                                    <tr>
                                        <td></td>
                                        <td>
                                            <Row>
                                                <Col span={7}></Col>
                                                <Col span={4}>
                                                    <Select
                                                        style={{ width: '90%' }}
                                                    >
                                                        <Option value="" key=""></Option>
                                                        <Option value="=" key="=">=</Option>
                                                        <Option value="" key=""></Option>
                                                    </Select>
                                                </Col>
                                                <Col span={10}>
                                                    <Input suffix=""/>
                                                </Col>
                                            </Row>
                                            <Row className="mt10">
                                                <Col span={7}></Col>
                                                <Col span={4}>
                                                    <Select
                                                        style={{ width: '90%' }}
                                                    >
                                                        <Option value="" key=""></Option>
                                                        <Option value="=" key="=">=</Option>
                                                        <Option value="" key=""></Option>
                                                    </Select>
                                                </Col>
                                                <Col span={10}>
                                                    <Input suffix=""/>
                                                </Col>
                                            </Row>
                                            <Row className="mt10">
                                                <Col span={7}></Col>
                                                <Col span={4}>
                                                    <Select
                                                        style={{ width: '90%' }}
                                                    >
                                                        <Option value="" key=""></Option>
                                                        <Option value="=" key="=">=</Option>
                                                        <Option value="" key=""></Option>
                                                    </Select>
                                                </Col>
                                                <Col span={10}>
                                                    <Input suffix=""/>
                                                </Col>
                                            </Row>
                                        </td>
                                        <td><Switch defaultChecked onChange={this.switchChange}/></td>
                                    </tr>
                                    <tr>
                                        <td></td>
                                        <td>
                                            <Row className="mt10">
                                                <Col span={7}></Col>
                                                <Col span={4}>
                                                    <Select
                                                        style={{ width: '90%' }}
                                                    >
                                                        <Option value="" key=""></Option>
                                                        <Option value="=" key="=">=</Option>
                                                        <Option value="" key=""></Option>
                                                    </Select>
                                                </Col>
                                                <Col span={10}>
                                                    <Input suffix=""/>
                                                </Col>
                                            </Row>
                                            <Row className="mt10">
                                                <Col span={7}></Col>
                                                <Col span={4}>
                                                    <Select
                                                        style={{ width: '90%' }}
                                                    >
                                                        <Option value="" key=""></Option>
                                                        <Option value="=" key="=">=</Option>
                                                        <Option value="" key=""></Option>
                                                    </Select>
                                                </Col>
                                                <Col span={10}>
                                                    <Input suffix=""/>
                                                </Col>
                                            </Row>
                                        </td>
                                        <td><Switch defaultChecked onChange={this.switchChange}/></td>
                                    </tr>
                                </table>
                                </Col>
                            </Row>
                            <div className="addProject-button">
                                <Button type="primary" htmlType="submit" ref="finish"></Button>
                                <Button type="primary" className="ml15" ref="save" onClick={()=>this.onSave()}></Button>
                                <Button type="primary" className="ml15" onClick={()=>this.onView()}></Button>
                                <Button className="ml15" onClick={()=>window.history.back()}></Button>
                            </div>
                        </Form>
                        </Form.Provider>
                    </div>
                    </Col>
                </Row>
                </div>
                <Modal
                    title=""
                    visible={this.state.policyVisible}
                    onOk={this.handleOk}
                    onCancel={this.handleCancel}
                    width="800px"
                    className="select-porject-modal"
                >
                    <Search
                        onSearch={value => console.log(value)}
                        style={{ width: 300 }}
                        enterButton
                    />
                    <Table rowSelection={rowSelection} columns={this.columns} dataSource={tableData ? tableData.result : []} pagination={false} rowKey="id" />
                </Modal>
            </div>
        );
    };
}

export default AddProject;